import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import authService from '../services/authService';
import { EstudianteDashboardData, UsuarioInfo } from '../types/auth';
import './Dashboard.css';

const EstudianteDashboard: React.FC = () => {
  const [dashboardData, setDashboardData] = useState<EstudianteDashboardData | null>(null);
  const [user, setUser] = useState<UsuarioInfo | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>('');
  const navigate = useNavigate();

  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    try {
      setLoading(true);
      
      // Verificar autenticación y rol
      if (!authService.isAuthenticated()) {
        navigate('/login');
        return;
      }

      if (!authService.isEstudiante()) {
        navigate('/admin/dashboard');
        return;
      }

      // Cargar datos del usuario y dashboard
      const [userData, estudianteData] = await Promise.all([
        authService.getCurrentUser(),
        authService.getEstudianteDashboard()
      ]);

      setUser(userData);
      setDashboardData(estudianteData);
      
    } catch (err: any) {
      console.error('Error cargando dashboard:', err);
      
      if (err.response?.status === 403) {
        setError('No tienes permisos de estudiante');
        setTimeout(() => navigate('/admin/dashboard'), 2000);
      } else if (err.response?.status === 401) {
        authService.logout();
        navigate('/login');
      } else {
        setError('Error cargando el dashboard');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = async () => {
    try {
      await authService.logout();
      navigate('/login');
    } catch (error) {
      console.error('Error en logout:', error);
      navigate('/login');
    }
  };

  const handleLogoutAll = async () => {
    try {
      await authService.logoutAll();
      navigate('/login');
    } catch (error) {
      console.error('Error en logout all:', error);
      navigate('/login');
    }
  };

  if (loading) {
    return (
      <div className="dashboard-container">
        <div className="loading">Cargando dashboard...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="dashboard-container">
        <div className="error-message">{error}</div>
      </div>
    );
  }

  return (
    <div className="dashboard-container">
      {/* Header */}
      <header className="dashboard-header">
        <div className="header-content">
          <div className="logo-section">
            <h1>ENARM360</h1>
            <span className="role-badge estudiante">ESTUDIANTE</span>
          </div>
          <div className="user-section">
            <div className="user-info">
              <span className="username">{user?.username}</span>
              <span className="email">{user?.email}</span>
            </div>
            <div className="logout-buttons">
              <button onClick={handleLogout} className="logout-btn">
                Cerrar Sesión
              </button>
              <button onClick={handleLogoutAll} className="logout-all-btn">
                Cerrar Todas
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="dashboard-main">
        <div className="welcome-section">
          <h2>{dashboardData?.message}</h2>
          <p>Tu plataforma de preparación para el ENARM</p>
        </div>

        {/* Stats Cards */}
        <section className="stats-section">
          <div className="stats-grid">
            <div className="stat-card estudiante">
              <div className="stat-icon">📚</div>
              <div className="stat-content">
                <h3>{dashboardData?.stats?.cursosInscritos}</h3>
                <p>Cursos Inscritos</p>
              </div>
            </div>
            <div className="stat-card estudiante">
              <div className="stat-icon">✅</div>
              <div className="stat-content">
                <h3>{dashboardData?.stats?.examenesCompletados}</h3>
                <p>Exámenes Completados</p>
              </div>
            </div>
            <div className="stat-card estudiante">
              <div className="stat-icon">🎯</div>
              <div className="stat-content">
                <h3>{dashboardData?.stats?.puntuacionPromedio}%</h3>
                <p>Puntuación Promedio</p>
              </div>
            </div>
          </div>
        </section>

        {/* Progress Section */}
        <section className="progress-section">
          <h3>Tu Progreso</h3>
          <div className="progress-card">
            <div className="progress-header">
              <h4>Preparación ENARM</h4>
              <span className="progress-percentage">{dashboardData?.stats?.puntuacionPromedio}%</span>
            </div>
            <div className="progress-bar">
              <div 
                className="progress-fill"
                style={{ width: `${dashboardData?.stats?.puntuacionPromedio}%` }}
              ></div>
            </div>
            <p>¡Sigue así! Estás mejorando constantemente.</p>
          </div>
        </section>

        {/* Actions Section */}
        <section className="actions-section">
          <h3>Acciones Disponibles</h3>
          <div className="actions-grid">
            {dashboardData?.actions?.map((action, index) => (
              <div key={index} className="action-card estudiante">
                <div className="action-icon">
                  {index === 0 ? '📝' : index === 1 ? '📊' : '🔍'}
                </div>
                <div className="action-content">
                  <h4>{action}</h4>
                  <p>
                    {index === 0 && 'Practica con simulacros del ENARM'}
                    {index === 1 && 'Revisa tu desempeño y estadísticas'}
                    {index === 2 && 'Consulta tus calificaciones anteriores'}
                  </p>
                </div>
                <button className="action-button estudiante">
                  Acceder
                </button>
              </div>
            ))}
          </div>
        </section>

        {/* Quick Info */}
        <section className="info-section">
          <div className="info-card">
            <h4>Nivel de Acceso</h4>
            <p className="access-level estudiante">{dashboardData?.accessLevel}</p>
          </div>
          <div className="info-card">
            <h4>Próximo Examen</h4>
            <p>Simulacro #13 - Medicina Interna</p>
          </div>
          <div className="info-card">
            <h4>Racha de Estudio</h4>
            <p>🔥 7 días consecutivos</p>
          </div>
        </section>
      </main>
    </div>
  );
};

export default EstudianteDashboard;